	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Message 1415 : Tue Aug  2 (Vakaur)     :: Changes</p>\n</blockquote>\n<p>The creation system has received quite a face lift:</p>\n<ul>\n<li>Certain annoying prompts that required fully typed \"yes\" or\n\"no\" answers have been changed to use the usual abbreviation\nsystem.</li>\n<li>Many steps in the creation process have had their text re-\nvamped to be more helpful (especially to new players).  The\n'help <class>' entries have received rewritings; the 'help\nrace'\nColour has also received some attention:</li>\n<li>There is now a default colour set, which may be enabled during\ncharacter creation or via the 'colour default' command.  The\n'help colour' entry has received an update.</li>\n<li>A new colour field \"enemy\" has been enabled, following a\nlengthy cleanup of the colour code.  The color field will\ncause all players on the opposing sides of the race war to\nappear in the specified colour.  If you manage to find any\nplaces where a player's real name is leaked instead of his\n\"starred\" race type, please let us know ASAP.</li>\n<li>RotS now supports \"high\" ANSI colours.  You can enable any of\nthese colours using the \"bright\" keyword in addition to the\ncolour you wish to make bright.  For example, new valid</li>\n</ul>\n<pre><code>colours include: \"bright white\", \"bright red\", etc.  See the\ncolour manual file for more detail.\n</code></pre>\n<p>Newbie kits are now available:</p>\n<ul>\n<li>Newbie kits are now available--once per death--in the safe\nroom where your race is reborn after death.  Note that the\ncontents of a newbie kit depend on: the receiver's level, race\nand class.  Signs have been erected in these places detailing\nthe use of the newbie kit system.\nCurrently, newbie kits are only available to whities.  This\nwill be changed as soon as possible.</li>\n</ul>\n<p>vakaur</p>","frontmatter":{"title":"News 1415: Changes","date":"October 09, 2017","author":null,"desc":null}}},"pathContext":{"slug":"/news/1415"}}

